.overlay-base {
  height: 100%;
  opacity: 0;
  width: 100%;
  display: none;
  position: fixed;
  top: 0;
}

.overlay-base.animate-appear {
  opacity: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: .3s ease-in-out forwards reveal;
  display: flex;
}

.overlay-base.animate-disappear {
  opacity: 1;
  pointer-events: none;
  animation: .3s ease-in-out reverse forwards reveal;
  display: block;
}

.overlay-base.animate-disappear * {
  pointer-events: none;
}

@keyframes reveal {
  from {
    opacity: 0;
  }

  to {
